html, body, #root {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  height: 100%;
  text-align: center;
}

.App-header {
  background-color: lightblue;
  height: 170px;
  padding: 50px 0 20px 0;
  color: white;
}

.App-title {
  margin-top: -5px;
  font-size: 2.5em;
}

.brain-slice {
  background: rgb(192, 238, 255);
  margin: 5px 0 0 0;
  cursor: crosshair;
}
.brain-slice-a, .brain-slice-b{
  width: 128px;
  height: 128px;
}
.brain-slice-c {
  width: 256px;
  height: 256px;
}

.brains {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 15px;
}

.brains-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.brains-right :first-child {
  height: 261px;
}

.brain-col {
  margin-right: 5px;
  position: relative;
}

.brains-left {
  text-align: right;
}
.caption-xy {
  position: absolute;
  width: 200px;
  left: 268px;
  top: 80px;
}
.caption-xz {
  position: absolute;
  top: 150px;
  right: 0;
}
.caption-yz {
  position: relative;
  /* left: 275px; */
  right: 0px;
}
.brains-right {
  text-align: left;
}
figure {
  margin: 0;
  position: relative;
  font-style: italic;
  font-family: cursive;
  font-size: x-large;
}

figure canvas {
  max-width: 100%;
  display: block;
  position: relative;
  z-index: 10;
}

figcaption {
  position: absolute;
  top: 0;
  left: 0;
  color: lightgray;
}

figcaption {
  height: 100%;
  opacity: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  /* box-shadow: 0 0 0 0px #2c3f52; */
  -webkit-transition: opacity 0.3s, height 0.3s, -webkit-box-shadow 0.3s;
  transition: opacity 0.3s, height 0.3s, -webkit-box-shadow 0.3s;
  -o-transition: opacity 0.3s, height 0.3s, box-shadow 0.3s;
  transition: opacity 0.3s, height 0.3s, box-shadow 0.3s;
  transition: opacity 0.3s, height 0.3s, box-shadow 0.3s, -webkit-box-shadow 0.3s;
}

.left-caption {
  width: 50px;
  right: 140px;
  left: unset;
}

figure:hover figcaption {
  opacity: 1;
  height: 100%;
}



